@use "./color";
.product {
  display: flex;
  flex-wrap: wrap;
  color: white;
  padding: 0 0 20px;
  box-sizing: border-box;
  background-color: color.$base-color;

  > div {
    width: 50%;
  }

  &__img {
    img {
      width: 100%;
      height: auto;
    }
  }

  &__content {
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;

    h1 {
      width: 30%;
    }

    p {
      width: 50%;
    }
  }
}

@media only screen and (max-width: 800px) {
  /* For tablets: */
  .product {
    flex-direction: column;

    > div {
      width: 100%;
    }

    &__content {
      h2 {
        text-align: center;
        width: 100%;
      }

      p {
        width: 75%;
      }
    }
  }
}
