@use "./color";
.branding {
  padding: 20px 0;
  box-sizing: border-box;
  display: flex;
  flex-wrap: wrap;
  color: white;
  background-color: color.$base-color;

  > div {
    width: 50%;
  }

  &__img {
    text-align: center;
    // display: flex;
    text-align: center;
    justify-content: center;
    // padding: ;
    img {
      position: relative;
      left: 0%;
      top: 30%;
      width: 15%;
      height: auto;
    }
  }

  &__content {
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;

    h1 {
      width: 30%;
    }

    p {
      width: 50%;
    }
  }
}

@media only screen and (max-width: 800px) {
  /* For tablets: */
  .branding {
    flex-direction: column-reverse;

    > div {
      width: 100%;
    }
    &__content {
      align-items: center;
      h2 {
        text-align: center;
        width: 100%;
      }

      p {
        width: 75%;
      }
    }
  }
}
